<template>
  <div class="common-layout">
    <el-container>
      <!-- 顶部条 -->
      <el-header>
        <el-row :gutter="20">
          <el-col :span="3">{{ $t("HomeView.title") }}</el-col>
          <el-col :span="17"></el-col>
          <el-col :span="4">
            <el-icon size="20" class="maximize" @click="maxHome = !maxHome"><FullScreen /></el-icon>
            <!-- 主题_选项 -->
            <el-switch
                class="themeBox"
                v-model="theme"
                active-action-icon="Watermelon"
                inactive-action-icon="MagicStick"
                @change="themeSwitchChange()"
            />
            <!-- 标签控制_选项 -->
            <el-dropdown @command="tagUp">
              <span class="el-dropdown-link">
                <el-icon size="20" class="item-box"><Guide /></el-icon>
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item command="1">
                    <el-icon><Refresh /></el-icon> {{ $t("HomeView.itemDropdown.refresh") }}
                  </el-dropdown-item>
                  <el-dropdown-item command="2">
                    <el-icon><SwitchButton /></el-icon> {{ $t("HomeView.itemDropdown.close") }}
                  </el-dropdown-item>
                  <el-dropdown-item command=""></el-dropdown-item>
                  <el-dropdown-item command="3">
                    <el-icon><DArrowLeft /></el-icon> {{ $t("HomeView.itemDropdown.closeLeft") }}
                  </el-dropdown-item>
                  <el-dropdown-item command="4">
                    <el-icon><DArrowRight /></el-icon> {{ $t("HomeView.itemDropdown.closeRight") }}
                  </el-dropdown-item>
                  <el-dropdown-item command=""></el-dropdown-item>
                  <el-dropdown-item command="5">
                    <el-icon><Rank /></el-icon> {{ $t("HomeView.itemDropdown.closeAll") }}
                  </el-dropdown-item>
                  <el-dropdown-item command="6">
                    <el-icon><Sort /></el-icon> {{ $t("HomeView.itemDropdown.closeOther") }}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
            <!-- 国际化_选项 -->
            <el-dropdown @command="switchover">
              <span class="el-dropdown-link">
                <svg style="color: white; width: 25px; height: 25px" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24" width="1.2em" height="1.2em" data-v-12008bb2=""><path fill="currentColor" d="m18.5 10l4.4 11h-2.155l-1.201-3h-4.09l-1.199 3h-2.154L16.5 10h2zM10 2v2h6v2h-1.968a18.222 18.222 0 0 1-3.62 6.301a14.864 14.864 0 0 0 2.336 1.707l-.751 1.878A17.015 17.015 0 0 1 9 13.725a16.676 16.676 0 0 1-6.201 3.548l-.536-1.929a14.7 14.7 0 0 0 5.327-3.042A18.078 18.078 0 0 1 4.767 8h2.24A16.032 16.032 0 0 0 9 10.877a16.165 16.165 0 0 0 2.91-4.876L2 6V4h6V2h2zm7.5 10.885L16.253 16h2.492L17.5 12.885z"></path></svg>
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item command="zhCn">
                    <el-icon><Football /></el-icon> {{ $t("HomeView.language.zh") }}
                  </el-dropdown-item>
                  <el-dropdown-item command="en">
                    <el-icon><Help /></el-icon> {{ $t("HomeView.language.en") }}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
            <!-- 头像_选项 -->
            <el-dropdown @command="handleCommand">
              <span class="el-dropdown-link">
                <el-avatar :size="35" :src="UserImage"/>
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item command="user"><el-icon><User/></el-icon>
                    {{ $t("HomeView.avatarDropdown.profile") }}
                  </el-dropdown-item>
                  <el-dropdown-item command="clearCache"><el-icon><Delete/></el-icon>
                    {{ $t("HomeView.avatarDropdown.clearCache") }}
                  </el-dropdown-item>
                  <el-dropdown-item command="LogOut"><el-icon><Notification/></el-icon>
                    {{ $t("HomeView.avatarDropdown.logout") }}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </el-col>
        </el-row>
      </el-header>

      <el-container>
        <!-- 左侧导航 -->
        <el-aside width="200px">
          <el-menu unique-opened active-text-color="#1890ff" background-color="#1f1f1f" class="el-menu-vertical-demo"
                   default-active="2" text-color="#fff" :router=true @select="menuHandleSelect">
            <el-sub-menu :index="menu.href" v-for="(menu, key, index) in menuList" :key="menu.id">
              <template #title>
                <el-icon>
                  <component :is="menu.icon"/>
                </el-icon>
                <span>{{ menu.name }}</span>
              </template>
              <el-menu-item :index="men.href" v-for="(men, key2, index2) in menu.resourceInfoList" :key="menu.id">
                <el-icon>
                  <component :is="men.icon"/>
                </el-icon>
                {{ men.name }}
              </el-menu-item>
            </el-sub-menu>
          </el-menu>
        </el-aside>

        <!-- 右侧视图 -->
        <el-main class="mainBox">
          <!-- 动态路由标签 -->
          <div class="roueDiv">
            <el-menu :router=true class="el-menu-demo" mode="horizontal" @select="tagHandleSelect" :default-active="defaultActive">
              <el-menu-item index="" :disabled="true"><el-icon size="14"><ArrowLeftBold /></el-icon></el-menu-item>
              <el-menu-item index="/home">{{ $t("HomeView.home") }}</el-menu-item>
              <el-menu-item :index="tag.href" v-for="(tag, key, index) in tagList" :key="tag.id">
                {{ tag.name }}
                <el-icon class="iconBox" size="12"><CloseBold @click.stop="deleteTag(tag)"/></el-icon>
              </el-menu-item>
            </el-menu>
          </div>
          <div class="breadcrumbDiv" v-if="isItem">
            <el-breadcrumb separator-class="ArrowRight">
              <el-breadcrumb-item :to="{ path: '/home' }" @click="deleteItem('/home')"> {{ $t("HomeView.home") }} </el-breadcrumb-item>
              <el-breadcrumb-item :index="index" v-for="(item, key, index) in itemList" :key="index">
                {{ item }}
              </el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <!-- 子路由渲染区 -->
          <el-main class="rouBox">
            <router-view @childClick="refreshHome"></router-view>
          </el-main>
        </el-main>
      </el-container>
    </el-container>

    <!-- 个人中心 -->
    <myView ref="myView" @childClick="refreshHome"></myView>
  </div>
</template>

<script>
import en from 'element-plus/dist/locale/en.mjs'
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'
import {getMenuList} from "@/http/api";
import {ElLoading, ElMessage} from "element-plus";
import myView from "@/views/home/MyView";
import {getSysTheme, setSysMaximize} from "@/util/system";

export default {
  name: "home",
  components: {myView},
  data() {
    return {
      // 树菜单
      menuList: [],
      // tag标签集合
      towResourceList: [],
      // 标签map key=href value=[父标题,子标题]
      towMap: new Map(),
      // 标签map key=href value=对象
      towItemMap: new Map(),
      user: {},
      UserImage: '',

      // 标签 {id: 0, name: '首页',  href:'/home'}
      tagList: [],
      // 默认选中 标签
      defaultActive: '/home',
      // 标题
      itemList: [],
      isItem: false,

      // 全屏开关
      maxHome: false,
      // 主题开关 true=暗黑 false=明亮
      theme: false,
    };
  },
  // watch 侦听器 能够侦听到data数据的变化 且获取对应的 新值 和 旧值
  watch: {
    // 监听全屏开关
    maxHome(newVal, oldVal) {
      setSysMaximize(newVal);
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getMenu(); // 获取菜单
    this.setTag(); // 从本地储存获取标签
    // 本地主题开关
    this.setTheme();
  },

  methods: {
    // 获取菜单
    getMenu() {
      // 获取本地用户信息 头像
      this.user = JSON.parse(window.localStorage.getItem("account"));
      this.UserImage = process.env.VUE_APP_BACKEND_API_URL + this.user.photoUrl;
      getMenuList().then((res) => {
        if (res.status == 200) {
          // 获取菜单 / 获取标签 / 获取标题
          this.menuList = res.data.data.resourceInfoList;
          this.towResourceList = res.data.data.towResourceList;

          // 储存标题map key=href value=父标题/子标题
          let allMap = new Map();
          let towList = [];
          res.data.data.towResourceList.forEach((item) => {
            allMap.set(item.id, item);
            this.towItemMap.set(item.href, item);
            if (item.resourceType == 2) {
              towList.push(item);
            }
          });
          towList.forEach((item) => {
            let p = allMap.get(item.pid);
            this.towMap.set(item.href, [p.name, item.name]);
          });
        }
      }).catch((erro) => { });
    },
    setTag() {
      // 获取本地标签
      let tagList = window.localStorage.getItem("tagList");
      if (tagList) {
        this.tagList = JSON.parse(tagList);
      }
      // 获取本地默认选中
      let tagDefaultActive = window.localStorage.getItem("tagDefaultActive");
      if (tagDefaultActive) {
        if (tagDefaultActive != '/home') {
          this.isItem = true;
        }
        this.defaultActive = tagDefaultActive;
      }
      // 获取本地默认选中
      let itemList = window.localStorage.getItem("itemList");
      if (itemList && itemList != 'undefined') {
        this.itemList = JSON.parse(itemList);
      }
    },
    // 菜单点击事件 index=菜单值 indexPath=菜单索引
    menuHandleSelect(index, indexPath) {
      this.isItem = true;
      this.itemList = this.towMap.get(index)
      window.localStorage.setItem("itemList", JSON.stringify(this.itemList));
      // 判断 index 是否存在于 tagList 中 存在则不添加
      let flag = false;
      this.tagList.forEach((item) => {
        if (item.href == index) {
          this.defaultActive = item.href;
          // 储存到本地 默认选中 刷新回显
          window.localStorage.setItem("tagDefaultActive", item.href);
          flag = true;
        }
      });
      if (flag) return;
      // 不存在则添加
      let tag = this.towItemMap.get(index);
      this.tagList.push(tag);
      this.defaultActive = index;
      window.localStorage.setItem("tagDefaultActive", index);
      window.localStorage.setItem("tagList", JSON.stringify(this.tagList));
    },
    // 标签点击事件
    tagHandleSelect(index, indexPath) {
      console.log('index', index)
      this.defaultActive = index;
      if (index == '/home') {
        window.localStorage.removeItem("itemList");
        window.localStorage.setItem("tagDefaultActive", index);
        this.isItem = false;
        return;
      }
      this.isItem = true;
      this.itemList = this.towMap.get(index)
      window.localStorage.setItem("itemList", JSON.stringify(this.itemList));
      // 储存到本地 默认选中 刷新回显
      window.localStorage.setItem("tagDefaultActive", index);
    },
    // 删除标签
    deleteTag(tag) {
      this.tagList.forEach((item, index) => {
        if (item.href == tag.href) {
          this.tagList.splice(index, 1);
          // 储存到本地 刷新回显
          window.localStorage.setItem("tagList", JSON.stringify(this.tagList));
        }
      });
      // 判断删除的是否是已经选中的
      if (this.defaultActive === tag.href) {
        if (this.tagList.length == 0) {
          this.defaultActive = '/home';
          this.$router.push(this.defaultActive);
          this.isItem = false;
          window.localStorage.setItem("tagDefaultActive", '/home');
          return;
        }
        // 选中最后一个tag 并进行跳转
        this.defaultActive = this.tagList[this.tagList.length - 1].href;
        this.$router.push(this.defaultActive);
        window.localStorage.setItem("tagDefaultActive", this.defaultActive);
        this.isItem = true;
        this.itemList = this.towMap.get(this.defaultActive)
        window.localStorage.setItem("itemList", JSON.stringify(this.itemList));
      }
    },
    // 删除标题 首页路由
    deleteItem(href) {
      this.isItem = false;
      this.defaultActive = href;
      window.localStorage.setItem("tagDefaultActive", href);
      window.localStorage.removeItem("itemList");
    },

    // 本地主题开关
    setTheme() {
      this.theme = getSysTheme();
    },
    // 主题开关
    themeSwitchChange() {
      // theme 主题开关 true=暗黑 false=明亮
      window.localStorage.setItem("themeSwitch", this.theme);
      // 调用父组件的方法
      this.$emit('childClick', 'theme');
      // 刷新网页
      location.reload();
    },
    // 标签控制_选项
    tagUp(command) {
      if (command == '') return;
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      // 刷新
      if (command == '1') {
        // 刷新网页
        location.reload();
      }
      // 主页不允许关闭
      if (this.defaultActive == '/home') {
        // 关闭全部标签
        if (command == '5' || command == '6') {
          this.defaultActive = '/home';
          window.localStorage.setItem("tagDefaultActive", this.defaultActive);
          this.isItem = false;
          this.itemList = [];
          window.localStorage.setItem("itemList", []);
          this.tagList = [];
          window.localStorage.setItem("tagList", []);
          // 跳转到主页
          this.$router.push('/HomePageView');
        }
        loading.close();
        return;
      }
      // 关闭标签
      if (command == '2') {
        this.tagList.forEach((item, index) => {
          if (item.href == this.defaultActive) {
            this.deleteTag(item);
            return;
          }
        });
      }
      // 关闭左侧标签
      if (command == '3') {
        let push = false;
        let tags = [];
        for (let i = 0; i < this.tagList.length; i++) {
          let item = this.tagList[i];
          if (item.href === this.defaultActive) {
            push = true;
          }
          if (push) {
            tags.push(item);
          }
        }
        this.tagList = tags;
        window.localStorage.setItem("tagList", JSON.stringify(this.tagList));
      }
      // 关闭右侧标签
      if (command == '4') {
        let push = true;
        let tags = [];
        for (let i = 0; i < this.tagList.length; i++) {
          let item = this.tagList[i];
          if (push) {
            tags.push(item);
          }
          if (item.href === this.defaultActive) {
            push = false;
          }
        }
        this.tagList = tags;
        window.localStorage.setItem("tagList", JSON.stringify(this.tagList));
      }
      // 关闭全部标签
      if (command == '5') {
        this.defaultActive = '/home';
        window.localStorage.setItem("tagDefaultActive", this.defaultActive);
        this.isItem = false;
        this.itemList = [];
        window.localStorage.setItem("itemList", []);
        this.tagList = [];
        window.localStorage.setItem("tagList", []);
        // 跳转到主页
        this.$router.push('/HomePageView');
      }
      // 关闭其他标签
      if (command == '6') {
        let tags = [];
        for (let i = 0; i < this.tagList.length; i++) {
          let item = this.tagList[i];
          if (item.href === this.defaultActive) {
            tags.push(item);
          }
        }
        this.tagList = tags;
        window.localStorage.setItem("tagList", JSON.stringify(this.tagList));
      }
      loading.close();
    },

    // 子组件调用父组件的方法 切换语言 值必须是语言文件的引入名称
    switchover(command) {
      if (command == 'en') {
        this.$confirm(this.$t("HomeView.languageDialog.enContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('en');
        });
      } else {
        this.$confirm(this.$t("HomeView.languageDialog.zhContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('zh');
        });
      }
    },
    // 切换语言 刷新首页框架数据 重新获取菜单 重新获取标签 重新获取标题 ...
    gotolanguage(locale) {
      const loading = ElLoading.service({
        lock: true, text: this.$t('common.loadingText'), background: 'rgba(0, 0, 0, 0.7)',}
      );
      this.$i18n.locale = locale; // 切换 I18n 语言包 选项式写法
      if (locale == 'en') {
        this.$emit('childClick', en);
        window.localStorage.setItem("navigatorLanguage", 'en-US');
      } else if (locale == 'zh'){
        this.$emit('childClick', zhCn);
        window.localStorage.setItem("navigatorLanguage", 'zh-CN');
      }
      // 清除本地的 标签.标题 设置默认选中为首页 跳转到主页
      window.localStorage.removeItem("tagList");
      window.localStorage.removeItem("itemList");
      window.localStorage.setItem("tagDefaultActive", '/home');
      this.defaultActive = '/home';
      this.isItem = false;
      this.tagList = [];
      this.itemList = [];
      this.$router.push('/HomePageView');
      loading.close();
      // 等1秒刷新
      setTimeout(() => {
        // 刷新网页
        location.reload();
      }, 500)
    },

    // 拉下点击事件 command=菜单值
    handleCommand(command) {
      if (command == "LogOut") {
        this.$confirm(this.$t("HomeView.logOutDialog.logoutTips"), this.$t("HomeView.logOutDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'warning'
        }).then(_ => {
          // 删除浏览器token其数据 跳转到登录页面 提示
          this.deWin();
          this.$router.push('/login');
          ElMessage({message: this.$t("HomeView.logOutDialog.logoutSuccess"), type: 'success',})
        }).catch(_ => {
          ElMessage({message: this.$t("HomeView.logOutDialog.logoutError"), type: 'info',})
        });
      } else if (command == 'user') {
        this.$nextTick(() => {
          this.$refs.myView.centerDialogVisible = true;
          this.$refs.myView.getPullDownData();
          this.$refs.myView.getForm();
        });
      } else if (command == 'clearCache') {
        // 刷新页面
        const loading = ElLoading.service({
          lock: true,
          text: this.$t("HomeView.clearCache"),
          background: 'rgba(0, 0, 0, 0.7)',
        })
        setTimeout(() => {
          loading.close()
          // 删除浏览器token其数据 跳转到登录页面 提示
          window.localStorage.clear();
          this.$router.push('/login');
          // 刷新网页
          location.reload();
        }, 1000)
      }
    },
    deWin() {
      window.localStorage.removeItem("account");
      window.localStorage.removeItem("token");
      window.localStorage.removeItem("tagList");
      window.localStorage.removeItem("itemList");
      window.localStorage.removeItem("tagDefaultActive");
    },


    // 组件操作后刷新主页数据
    refreshHome(refreshData) {
      console.log('HomePage', refreshData)
      if (refreshData.type == 'user') {
        // 更新个人中心数据 home同步更新方法
        let userStr = JSON.parse(window.localStorage.getItem("account"));
        userStr.photoUrl = refreshData.data.photoUrl;
        window.localStorage.setItem('account', JSON.stringify(userStr));
        this.UserImage = process.env.VUE_APP_BACKEND_API_URL + refreshData.data.photoUrl;
      } else if (refreshData.type == 'HomePage') {
        // 组件跳转时触发 进行设置标签/默认选择/标题
        this.menuHandleSelect(refreshData.herf, null);
      }
    },
  }

}
</script>

<style scoped>
/* 设置主页分栏 顶部60px 导航和视图填充剩余屏幕高度*/
.common-layout {
  display: flex;
  flex-direction: column;
  height: 100vh; /* 设置高度为视窗高度 */
}
.common-layout el-container {
  flex: 1; /* 填充剩余空间 */
}

/* 覆盖组件的样式 mian的内边距 */
.el-main /deep/ {
  --el-main-padding: 0px;
}
/* 将视图增加内边距 */
main > div[id] {
  padding: 20px;
}

/* 左侧导航的样式 */
.el-menu-vertical-demo:not(.el-menu--collapse) {
  /*height: 851px;*/
}
.about {
  width: 100%;
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col-4 {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  color: white;
}
.maximize {
  cursor: pointer;
  color: white;
  display: flex;
  margin-right: 8px;
  align-items: center;
}
.maximize:hover {
  color: #79bbff;
  cursor: pointer;
}
.item-box {
  cursor: pointer;
  color: white;
  display: flex;
  align-items: center;
}
.item-box:hover {
  color: #79bbff;
  cursor: pointer;
}
/*个人头像*/
.el-dropdown {
  padding-right: 10px;
}
.el-header,
.el-footer {
  background-color: #1f1f1f;
  color: #ffffff;
  line-height: 60px;
}
.el-aside {
  background-color: #1f1f1f;
  color: #ffffff;
}
.el-main {
  background-color: #ffffff;
  color: #333;
}
body > .el-container {
  margin-bottom: 40px;
}
.el-container {
  height: 100%;
}
.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}
.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
.el-menu {
  height: 100%;
}
/*导航二级分组样式*/
.el-menu-item-group {
  padding: 0px 0px 0px 0px;
}
.example-showcase .el-dropdown-link {
  cursor: pointer;
  color: var(--el-color-primary);
  display: flex;
  align-items: center;
}
.mainBox {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.rouBox{
  flex: 1; /* 填充剩余空间 */
}
