<template>
  <div id="myView">
    <el-dialog draggable v-model="centerDialogVisible" :before-close="beforeClose" :title="$t('MyView.userForm.label')" width="52%" style="height: 600px" center>
    <div>
      <el-divider content-position="right">
        {{ $t('MyView.userForm.title') }} {{ form.nickName }}
      </el-divider>
      <el-tabs tab-position="left" style="height: 430px;" class="demo-tabs">
        <el-tab-pane :label="$t('MyView.userForm.label')" class="tabDiv">
          <el-form :model="form" label-width="120px">
            <el-row :gutter="3">
              <el-col :span="5"><div class="grid-content ep-bg-purple" />
                <el-upload
                    class="avatar-uploader"
                    action="/api/uploadFile/uploadFile"
                    :headers="headers" :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload"
                    :data="fileData"
                >
                  <img v-if="imageUrl" :src="imageUrl" :class="{ 'editable-form': !isReadonly }" class="avatar" />
                  <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
                </el-upload>
              </el-col>
              <el-col :span="19"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('MyView.userForm.account')" :label-width="formLabelWidth">
                  <el-input v-model="form.account" :readonly="isReadonly" :placeholder="$t('MyView.userForm.accountPlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
                </el-form-item>
                <el-form-item :label="$t('MyView.userForm.nickName')" :label-width="formLabelWidth">
                  <el-input v-model="form.nickName" :readonly="isReadonly" :placeholder="$t('MyView.userForm.nickNamePlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
                </el-form-item>
                <el-row>
                  <el-col :span="15"><div class="grid-content ep-bg-purple" />
                    <el-form-item :label="$t('MyView.userForm.name')" :readonly="isReadonly"  :label-width="formLabelWidth">
                      <el-input v-model="form.name" :placeholder="$t('MyView.userForm.namePlaceholder')" :class="{ 'editable-form': !isReadonly }"/>
                    </el-form-item>
                  </el-col>
                  <el-col :span="9"><div class="grid-content ep-bg-purple-light" />
                    <el-form-item :label="$t('MyView.userForm.sex')" :label-width="formLabelWidth" style="width: 100%">
                      <el-select filterable v-model="form.sex" :placeholder="$t('MyView.userForm.sexPlaceholder')" :class="{ 'editable-form': !isReadonly }" :disabled="isReadonly">
                        <el-option :value="0" :label="$t('pullDown.sex.secret')"/>
                        <el-option :value="1" :label="$t('pullDown.sex.man')"/>
                        <el-option :value="2" :label="$t('pullDown.sex.woman')"/>
                      </el-select>
                    </el-form-item>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('MyView.userForm.role')" :label-width="formLabelWidth">
                  <el-select filterable v-model="form.role" :placeholder="$t('MyView.userForm.rolePlaceholder')" :disabled="true">
                    <el-option v-for="item in selectData.roleList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('MyView.userForm.region')" :label-width="formLabelWidth">
                  <el-select v-model="form.region" filterable :placeholder="$t('MyView.userForm.regionPlaceholder')" :disabled="true" >
                    <el-option v-for="item in selectData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('MyView.userForm.phone')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.phone" :placeholder="$t('MyView.userForm.phonePlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('MyView.userForm.identityCard')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.identityCard" :placeholder="$t('MyView.userForm.identityCardPlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="8"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('MyView.userForm.birthDate')" :label-width="formLabelWidth">
                  <div :class="{ 'editable-form': !isReadonly }" class="el-input">
                    <el-date-picker v-model="form.birthDate" type="date" :placeholder="$t('MyView.userForm.birthDatePlaceholder')"  :disabled="isReadonly" style="width: 100%;"></el-date-picker>
                  </div>
                </el-form-item>
              </el-col>
              <el-col :span="8"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('MyView.userForm.userStatus')" :label-width="formLabelWidth">
                  <el-select v-model="form.userStatus" :placeholder="$t('MyView.userForm.userStatusPlaceholder')" :disabled="true">
                    <el-option :value="1" :label="$t('pullDown.userStatus.enable')"/>
                    <el-option :value="2" :label="$t('pullDown.userStatus.disable')"/>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12"><div class="grid-content ep-bg-purple" />
                <el-form-item :label="$t('MyView.userForm.weixin')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.weixin" :placeholder="$t('MyView.userForm.weixinPlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
                </el-form-item>
              </el-col>
              <el-col :span="12"><div class="grid-content ep-bg-purple-light" />
                <el-form-item :label="$t('MyView.userForm.qq')" :label-width="formLabelWidth">
                  <el-input :readonly="isReadonly" v-model="form.qq" :placeholder="$t('MyView.userForm.qqPlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item :label="$t('MyView.userForm.email')" :label-width="formLabelWidth">
              <el-input :readonly="isReadonly" v-model="form.email" :placeholder="$t('MyView.userForm.emailPlaceholder')" :class="{ 'editable-form': !isReadonly }" autocomplete="off"/>
            </el-form-item>
            <div class="budiv">
              <el-button type="primary" v-show="isReadonly" @click="isReadonly = false" plain>
                {{ $t('common.button.edit') }}
              </el-button>
              <el-button type="success" v-show="!isReadonly" @click="upUser" plain>
                {{ $t('common.button.confirm') }}
              </el-button>
              <el-button type="danger" v-show="!isReadonly" @click="upUserCancel" plain>
                {{ $t('common.button.cancel') }}
              </el-button>
              <el-button type="info" @click="closeButton" plain>
                {{ $t('common.button.back') }}
              </el-button>
            </div>
          </el-form>
        </el-tab-pane>
        <el-tab-pane :label="$t('MyView.updatePass.label')">
          <el-row :gutter="20">
            <el-col :span="6"><div class="grid-content ep-bg-purple" /></el-col>
            <el-col :span="12"><div class="grid-content ep-bg-purple" />
              <el-form ref="passForm" :rules="rules" label-position="right" label-width="80px" :model="passForm" style="max-width: 460px">
                <el-form-item :label="$t('MyView.updatePass.account')">
                  <el-input v-model="form.account" :readonly="true"/>
                </el-form-item>
                <el-form-item :label="$t('MyView.updatePass.password') " prop="password">
                  <el-input type="password" v-model="passForm.password" show-password :placeholder="$t('MyView.updatePass.passwordPlaceholder')"/>
                </el-form-item>
                <el-form-item :label="$t('MyView.updatePass.newPassword')" prop="newPassword">
                  <el-input type="password" v-model="passForm.newPassword" show-password :placeholder="$t('MyView.updatePass.newPasswordPlaceholder')"/>
                </el-form-item>
                <el-form-item :label="$t('MyView.updatePass.newPassword')" prop="newTowPassword" >
                  <el-input type="password" v-model="passForm.newTowPassword" show-password :placeholder="$t('MyView.updatePass.newTowPasswordPlaceholder')"/>
                </el-form-item>
                <div class="budiv">
                  <el-button type="success"  @click="upUserPassWord" plain>
                    {{ $t('common.button.confirm') }}
                  </el-button>
                  <el-button type="warning"  @click="passForm = {}" plain>
                      {{ $t('common.button.reset') }}
                  </el-button>
                  <el-button type="info" @click="closeButton" plain>
                    {{ $t('common.button.back') }}
                  </el-button>
                </div>
              </el-form>
            </el-col>
            <el-col :span="6"><div class="grid-content ep-bg-purple" /></el-col>
          </el-row>
        </el-tab-pane>

<!--        <el-tab-pane label="Role">Role</el-tab-pane>-->
      </el-tabs>
    </div>
<!--      <template #footer>-->
<!--      <span class="dialog-footer">-->
<!--        <el-button @click="centerDialogVisible = false">关闭</el-button>-->
<!--        <el-button type="primary" @click="centerDialogVisible = false">-->
<!--          Confirm-->
<!--        </el-button>-->
<!--      </span>-->
<!--      </template>-->
    </el-dialog>
  </div>
</template>

<script>

import {updateUserAdmin, updateUserPassword, userGetData} from "@/http/basics/userApi";
import {ElNotification} from "element-plus";
import {getRegionPullDown} from "@/http/clienteleApi";
import {getRolePullDown} from "@/http/PullDownApi";

export default {
  name: 'myView',
  components: {},
  data: function () {
    return {
      // 开关
      centerDialogVisible: false,
      formLabelWidth: '80px',
      selectData: {
        regionList: [],
        roleList: [],
      },

      // 用户表单数据
      isReadonly: true,
      form: {},
      passForm: {},
      rules: {
        password: [
          { required: true, message: this.$t('MyView.updatePass.passwordPlaceholder'), trigger: 'blur' },
          { min: 6, max: 20, message: this.$t('MyView.updatePass.passwordLength'), trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: this.$t('MyView.updatePass.newPasswordPlaceholder'), trigger: 'blur' },
          { min: 6, max: 20, message: this.$t('MyView.updatePass.passwordLength'), trigger: 'blur' }
        ],
        newTowPassword: [
          { required: true, message: this.$t('MyView.updatePass.newTowPasswordPlaceholder'), trigger: 'blur' },
          { min: 6, max: 20, message: this.$t('MyView.updatePass.passwordLength'), trigger: 'blur' },
          { validator: (rule, value, callback) => {
              if (value !== this.passForm.newPassword) {
                callback(new Error(this.$t('MyView.updatePass.passwordInconsistent')));
              } else {
                callback();
              }
            }, trigger: 'blur' }
        ],
      },

      // 上传请求的请求头 存放token校验
      headers: {},
      // 请求接口需要携带的参数
      fileData: {
        folder: 'userPhotoUrl'
      },
      // 上传成功之后的图片地址
      imageUrl: '',

      // 更新主页对象
      refreshData: {
        type: 'user',
        data: {},
      },
    };
  },
  created() {
    // 后端如果验证 token 会报空 因为请求不是异步不会被路由守卫装配token 所以需要在发送请求时带上token
    this.headers = {token: localStorage.getItem("token")};
  },
  mounted() {
    this.getPullDownData();
    this.getForm();
  },
  methods: {
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },

    getPullDownData() {
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.selectData.regionList = res.data.data;
      }).catch((erro) => { });
      // 获取角色的数据
      getRolePullDown().then(res => {
        if (res.data.status == 200) this.selectData.roleList = res.data.data;
      }).catch((erro) => {
      });
    },
    getForm() {
      userGetData().then(res => {
        if (res.data.status == 200) {
          this.form = res.data.data;
          this.imageUrl = this.getUrl(this.form.photoUrl);
        }
      }).catch((erro) => { });
    },
    // 确认修改用户信息
    upUser() {
      updateUserAdmin(this.form).then(res => {
        if (res.data.status == 200) {
          ElNotification({title: this.$t('common.message.updateSuccess'), message: res.data.msg, type: 'success',})
          this.getForm();
          this.isReadonly = true;
          // 修改成功之后联动修改本地储存的头像显示
          this.refreshData.data = this.form;
          this.$emit('childClick', this.refreshData);
        }
      }).catch((erro) => { });
    },
    // 修该用户信息 - 取消按钮
    upUserCancel() {
      this.isReadonly = true;
      this.getForm();
    },
    // 点击遮罩和x时执行
    beforeClose(done) {
      this.closeButton();
      done(); // 必须调用 done，以继续关闭对话框
    },
    // 返回按钮
    closeButton() {
      this.$refs.passForm.resetFields(); // 重置表单验证
      this.isReadonly = true;
      this.centerDialogVisible = false;
      this.form = {};
      this.passForm = {};
    },

    upUserPassWord() {
      this.$refs.passForm.validate((valid) => {
        if (valid) {
          this.passForm.id = this.form.id;
          this.passForm.account = this.form.account;
          updateUserPassword(this.passForm).then(res => {
            if (res.data.status == 200) {
              ElNotification({title: this.$t('common.message.updateSuccess'), message: res.data.msg, type: 'success',})
              this.passForm = {};
            }
          }).catch((erro) => { });
        } else {
          return false;
        }
      });
    },

    // 上传之后的res结果集
    handleAvatarSuccess(res, file) {
      if (res.status == 200) {
        // vue.config.js 定义了后端请求前缀路径 可直接调用
        this.form.photoUrl = res.data;
        this.imageUrl = process.env.VUE_APP_BACKEND_API_URL + res.data;
      } else {
        this.$message.error(this.$t('common.message.networkError'));
      }
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadError'), type: 'info',})
      }
      if (!isLt2M) {
        ElNotification({title: this.$t('common.message.tipsTitle'), message: this.$t('common.message.uploadSizeError'), type: 'info',})
      }
      return isJPG && isLt2M;
    }
  },
}
</script>

